body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background: #20262E;
  font-family: Helvetica;
}

#app {
  background: #fff;
  border-radius: 4px;
  padding: 20px;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}

li {
  margin: 8px 0;
}

h2 {
  font-weight: bold;
  margin-bottom: 15px;
}

.done {
  color: rgba(0, 0, 0, 0.3);
  text-decoration: line-through;
}

input {
  margin-right: 5px;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.main-container{
  max-width: 342px;
  padding: 20px;
  padding-top: 30px;
  margin-top: 20px;
  background: rgb(255,190,0);
  border: 5px solid rgb(240,174,0);
  border-radius: 10px;
}

.reactris-container{
  margin-left: 35px;
  margin-right: 35px;
}

.screen{
  width: 100%;
  height: 100%;
  min-width: 200px;
  min-height: 250px;
  display: inline-block;
  margin-bottom: 20px;
  padding: 5px;
  background: #9ead86;
  border: 5px solid;
  border-top-color: currentcolor;
  border-right-color: currentcolor;
  border-bottom-color: currentcolor;
  border-left-color: currentcolor;
  border-color: #987f0f rgb(255,210,0) rgb(255,205,0) #987f0f;
}
.table{
  padding: 0;
  border-bottom: 5px solid black;
}
.row{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 0;
  margin: 0;
}
.col{
  width: 100%;
  padding-top: 8.73%;
  position: relative;
  margin: 0;
  border: 2px solid #879372;
}
.col::after{
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #8E9A79;
}
.filled{
  border-color: black;
}
.filled::after{
  background: black;
}
.empty{
  border-color: #879372;
}


@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@media(max-width: 768px){
  .main-container{
    max-width: 100%;
    margin: 0;
    border-radius: 0;
  }
  .reactris-container{
    
  }
}

.info{
    height: 15px;
    padding: 0;
    margin-left: 1px;
    margin-right: 1px;
    color: black;
    font-size: 15px;
    font-weight: 800;
    text-align: left;
}

.score{
    float: right;
    white-space: pre-wrap;
}

.blink {
    animation: blink-animation 1s steps(2, start) infinite;
    -webkit-animation: blink-animation 1s steps(2, start) infinite;
    font-size: 18px;
}
@keyframes blink-animation {
    to {
        visibility: hidden;
    }
}
@-webkit-keyframes blink-animation {
    to {
        visibility: hidden;
    }
}
.controls .container{
    display: -webkit-flex;
    display: flex;
    height: 150px;
    position: relative;
    text-align: left;
}
.controls .spacer{
    position: relative;
    display: inline-block;
    width: 40px;
    height: 40px;
    box-shadow: inset 0px 0px 15px rgba(0,0,0,0.5);
    text-align: center;
}
.controls .buttons{
    font-size: 13px;
    font-weight: 700;
}
.controls .button{
    display: inline-block;
    text-align: center;
}
.controls .button + .button{
    margin-left: 15px;
}
.controls .buttons button{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    border: none;
    box-shadow: 0px 0px 5px black;
    background: rgb(155,0,0);
    margin-bottom: 5px;
}
.controls .buttons button:active{
    box-shadow: inset 0px 0px 10px black;
}



.controls .arrows{
    max-width: 150px;
    position: absolute;
    right: 0;
    top: 15px;
}
.controls .arrows .left-right{
    display: -webkit-flex;
    display: flex;
}
.controls .arrows button{
    width: 30px;
    height: 40px;
    font-size: 20px;
    border: none;

    line-height: 40px;
    vertical-align: middle;
    text-align: center;
    padding: 0 10px;
    color: #ffffff;
    background-color: black;
    position: relative;
    display: inline-block;
}
.controls .arrows button:active{
    background-color:rgb(155,0,0);
}

/*Arrow Right*/
.controls .arrows .arrow-right:after{
    position: absolute;
    right: -14px;
    top: 0;
    content: " ";
    border-style: solid;
    border-width: 20px 0 20px 15px;
    border-color: transparent transparent transparent black;
}
.controls .arrows .arrow-right:active:after{
    border-color: transparent transparent transparent rgb(155,0,0);
}

/*Arrow Left*/
.controls .arrows .arrow-left:before{
    position: absolute;
    left: -14px;
    top: 0;
    content: " ";
    border-style: solid;
    border-width: 20px 0 20px 15px;
    border-color: transparent transparent transparent black;
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
}
.controls .arrows .arrow-left:active:before{
    border-color: transparent transparent transparent rgb(155,0,0);
}

/*Arrow Up*/
.controls .arrows .arrow-up{
    width: 40px;
    height: 30px;
}
.controls .arrows .arrow-up:before{
    position: absolute;
    top: -26.5px;
    left: 13px;
    content: " ";
    border-style: solid;
    border-width: 20px 0 20px 15px;
    border-color: transparent transparent transparent black;
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
}
.controls .arrows .arrow-up:active:before{
    border-color: transparent transparent transparent rgb(155,0,0);
}

/*Arrow Down*/
.controls .arrows .arrow-down{
    width: 40px;
    height: 30px;
}
.controls .arrows .arrow-down:after{
    position: absolute;
    bottom: -26.5px;
    left: 13px;
    content: " ";
    border-style: solid;
    border-width: 20px 0 20px 15px;
    border-color: transparent transparent transparent black;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
}
.controls .arrows .arrow-down:active:after{
    border-color: transparent transparent transparent rgb(155,0,0);
}

/*mini arrows*/
i{
    border: solid black;
    border-width: 0 2px 2px 0;
    display: inline-block;
    padding: 2px;
    position: absolute;
}
.spacer .up{
    transform: rotate(-135deg);
    -webkit-transform: rotate(-135deg);
    top: 7px;
    left: 43%;
}
.spacer .down{
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
    bottom: 7px;
    left: 43%;
}
.spacer .left{
    transform: rotate(135deg);
    -webkit-transform: rotate(135deg);
    left: 7px;
    top: 43%;
}
.spacer .right{
    transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg);
    right: 7px;
    top: 43%;
}
